.history__wrapper {
  @media screen and (min-width: $tablet) {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    column-gap: 60px;

    height: 672px;
  }

  @media screen and (min-width: $desktop) {
    column-gap: 150px;

    height: 747px;
  }
}

.history__text {
  margin-bottom: 32px;

  @media screen and (min-width: $tablet) {
    width: 294px;
  }

  @media screen and (min-width: $desktop) {
    width: 400px;
  }

  &--with-margin {
    @media screen and (min-width: $tablet) {
      margin-bottom: 85px;
    }

    @media screen and (min-width: $desktop) {
      margin-bottom: 74px;
    }
  }

  &--order-2 {
    @media screen and (min-width: $tablet) {
      margin-bottom: 0;
      order: 2;
    }

    @media screen and (min-width: $desktop) {
      width: 510px;
    }
  }
}

// Figure
.history__figure {
  @media screen and (min-width: $tablet) {
    display: flex;
    // justify-content: space-between;
    align-items: flex-end;

    margin-top: -27px;
  }
  @media screen and (min-width: $desktop) {
    margin-top: -7px;
  }
}

// Image
.history__thumb {
  margin-bottom: 32px;

  &--order-1 {
    @media screen and (min-width: $tablet) {
      order: 1;
    }
  }
}

.history__figure-thumb {
  @media screen and (min-width: $tablet) {
    flex-shrink: 0;
    margin-bottom: 0;
    margin-right: 60px;
  }

  @media screen and (min-width: $desktop) {
    margin-right: 260px;
  }
}

.history__image {
  @media screen and (max-width: ($tablet - 1px)) {
    margin: 0 auto;
  }
}

// Blockquote
.history__figure-wrapper {
  @media screen and (min-width: $tablet) {
    padding-right: var(--padding);
  }

  @media screen and (min-width: $desktop) {
    padding-right: 318px;
  }
}

.history__figure-blockquote {
  margin-bottom: 16px;

  font-weight: 600;
  font-size: 20px;
  line-height: 1.25;
  color: $title-text-color;

  &::before {
    content: '';
    display: block;
    margin-bottom: 24px;

    width: 34px;
    height: 25px;

    background-image: url("data:image/svg+xml,%3Csvg width='34' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 12.04V0h13.965v9.065C13.965 18.508 9.31 23.82 0 25v-4.957c2.352-.614 3.96-1.511 4.823-2.692.912-1.18 1.464-2.95 1.656-5.311H0ZM20.035 12.04V0H34v9.065C34 18.508 29.345 23.82 20.035 25v-4.957c2.351-.614 3.96-1.511 4.823-2.692.912-1.18 1.464-2.95 1.656-5.311h-6.48Z' fill='%23DF3D3A'/%3E%3C/svg%3E");

    @media screen and (min-width: $tablet) {
      margin-bottom: 52px;
      max-width: 294px;
    }

    @media screen and (min-width: $desktop) {
      margin-bottom: 50px;
    }
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 32px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 28px;
  }
}

// Caption
.hostory__figure-caption {
  font-size: 14px;
  line-height: 1.25;
  color: rgba($primary-text-color, 0.4);

  @media screen and (min-width: $tablet) {
    font-size: 16px;
    line-height: 0.7;
  }
}
