h1, h2, h3, p, ul, figure, blockquote {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: inline-block;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input {
  outline: none;
}

button {
  cursor: pointer;
  color: inherit;
  border: none;
  padding: 0;
}

svg {
