body {
  color: rgba(255, 255, 255, .8);
  background-color: #191919;
  font-family: Gilroy;
}

body.scroll-lock {
  overflow: hidden;
}

.button {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .02em;
  color: rgba(255, 255, 255, .9);
  padding-top: 19px;
  padding-bottom: 19px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.25;
}

@media screen and (min-width: 768px) {
  .button {
    width: 294px;
  }
}

@media screen and (min-width: 1440px) {
  .button {
    width: 400px;
    padding-top: 22px;
    padding-bottom: 21px;
    font-size: 20px;
    line-height: 1.25;
  }
}

.button--primary {
  background-color: #df3d3a;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0) 50%, #b5302d 50%);
  background-size: 200% 100%;
  transition: background-position .3s cubic-bezier(.19, 1, .22, 1) .1s, color .5s, background-color .5s;
}

.button--primary:hover, .button--primary:focus {
  color: #fff;
  background-color: #b5302d;
  background-position: -100% 100%;
}

.button--secondary {
  background: none;
  border: 0;
  padding-top: 20px;
  padding-bottom: 18px;
  transition: color .25s;
  position: relative;
  box-shadow: inset 0 0 0 1px #df3d3a;
}

@media screen and (min-width: 1440px) {
  .button--secondary {
    padding-top: 22px;
    padding-bottom: 21px;
  }
}

.button--secondary:before, .button--secondary:after {
  content: "";
  width: 0;
  height: 0;
  border: 1px solid rgba(0, 0, 0, 0);
  position: absolute;
}

.button--secondary:before, .button--secondary:after {
  top: 0;
  left: 0;
}

.button--secondary:hover {
  color: #b5302d;
}

.button--secondary:hover:before, .button--secondary:hover:after {
  width: 100%;
  height: 100%;
}

.button--secondary:hover:before {
  border-top-color: rgba(255, 255, 255, .8);
  border-right-color: rgba(255, 255, 255, .8);
  transition: width .25s ease-out, height .25s ease-out .25s;
}

.button--secondary:hover:after {
  border-bottom-color: rgba(255, 255, 255, .8);
  border-left-color: rgba(255, 255, 255, .8);
  transition: height .25s ease-out, width .25s ease-out .25s;
}

